
          @import "src/assets/styles/_variables.scss";
          @import "src/assets/styles/_utils.scss";
        
.container {
  justify-content: center;
}

.pwedit {
  width: vw(350px);
  &_top {
    .divide {
      width: 100%;
      height: vw(1px);
      background-color: $GRAY-F3;
      margin: vw(64px) 0 vw(28px);
    }
    &_title {
      color: $GRAY-40;
      margin: vw(140px) 0 vw(40px) 0;
    }
    img {
      height: vw(120px);
      width: vw(120px);
      border-radius: 70%;
    }
  }

  &_content {
    width: vw(300px);
    margin: 0 vw(25px) 0 vw(25px);
    &_title {
      color: $GRAY-67;
      margin: (14px) 0 (12px);
    }
    input {
      width: vw(300px);
      height: vw(44px);
      border-radius: vw(6px);
      border: vw(1px) solid $MAIN;
      color: $GRAY-40;
      padding-left: vw(16px);
      margin-bottom: vw(6px);
      &:focus {
        box-shadow: 0 0 vw(5px) $MAIN;
      }
    }
  }
  &_btn {
    margin-top: vw(40px);
    width: vw(300px);
    height: vw(52px);
    color: $WHITE;
    background-color: $MAIN;
    border-radius: vw(6px);
    margin-top: vw(11px);
    &:hover {
      box-shadow: 0 0 vw(5px) $MAIN;
    }
  }
}
