
          @import "src/assets/styles/_variables.scss";
          @import "src/assets/styles/_utils.scss";
        
.container {
  justify-content: center;
}
.observer {
  margin-top: vw(20px);
  height: 300px;
  width: 300px;
}
.picture {
  flex-direction: column;
  width: (1150px);
  &_best {
    margin-top: (60px);
    margin-bottom: (90px);

    &_week {
      color: $GRAY-40;
      margin-bottom: (8px);
    }

    &_title {
      color: $GRAY-67;
      margin-bottom: (40px);
    }
  }

  &_camp {
    &_title {
      margin-bottom: (60px);
      justify-content: space-between;
      flex-direction: row;

      &_text {
        color: $GRAY-40;
      }

      &_btn {
        padding: (7px) (28px);
        height: (40px);
        color: $WHITE;
        background-color: $MAIN;
        border-radius: (17px);
      }
    }
  }
}

.best_photo_list,
.commu_photo_list {
  width: (1150px);
  justify-content: space-between;
  flex-wrap: wrap;
}

@media screen and (max-width: $DESKTOP-VW) {
  .picture {
    width: vw(1150px);
    &_best {
      margin-top: vw(60px);
      margin-bottom: vw(90px);

      &_week {
        margin-bottom: vw(8px);
      }

      &_title {
        margin-bottom: vw(40px);
      }
    }

    &_camp {
      &_title {
        margin-bottom: vw(60px);

        &_btn {
          padding: vw(7px) vw(28px);
          height: vw(40px);
          border-radius: vw(17px);
        }
      }
    }
  }

  .comp {
    width: vw(1150px);
  }
}
@media screen and (max-width: $TABLET-VW) {
  .picture {
    width: vw-t(950px);
    &_best {
      margin-top: vw-t(60px);
      margin-bottom: vw-t(90px);

      &_week {
        margin-bottom: vw-t(8px);
      }

      &_title {
        margin-bottom: vw-t(50px);
      }
    }

    &_camp {
      &_title {
        margin-bottom: vw-t(60px);

        &_btn {
          padding: vw-t(7px) vw-t(28px);
          height: vw-t(40px);
          border-radius: vw-t(17px);
        }
      }
    }
  }
  .best_photo_list,
  .commu_photo_list {
    width: vw-t(950px);
  }
}

@media screen and (max-width: $PHONE-VW) {
  .picture {
    width: vw-p(570px);
    &_best {
      margin-top: vw-p(20px);
      margin-bottom: vw-p(60px);

      &_week {
        margin-bottom: vw-p(8px);
      }

      &_title {
        margin-bottom: vw-p(40px);
      }
    }

    &_camp {
      &_title {
        margin-bottom: vw-p(30px);

        &_btn {
          padding: vw-p(7px) vw-p(28px);
          height: vw-p(40px);
          border-radius: vw-p(17px);
        }
      }
    }
  }
  .best_photo_list,
  .commu_photo_list {
    // width: vw-t(570px);
  }
}
